.imageTable {
  width: 75%;
  position: absolute;
  height: 650px;
  margin: 5% auto;
  padding-left: 150px;
  right: 5%;
}
body {
  overflow: none;
  // background-image: "../";
}
.scene {
  overflow: none;
  width: 70%;
  margin: auto;
  height: 100vh;
}

.popup {
  width: 450px;
  height: 400px;
  margin: 4% auto;
  padding: 10px;
  position: fixed;
  top: 10%;
  font-size: 19px;
  color: #043461;
  left: 40%;
  background-color: #f3f4f4;
  border-radius: 10px;
  // color: white;
  box-shadow: rgba(29, 29, 29, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  .modal {
    position: absolute;
    right: 0%;
    color: #043461;
    top: -6%;
  }
}
.timer {
  width: 200px;
  height: 100px;
  // background-color: white;
  position: absolute;
  top: 1%;
  font-size: 20px;
  color: #043461;
  right: 2%;
  // border: 1px solid;
  border-radius: 5px;
  box-shadow: rgba(29, 29, 29, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.RC1 {
  position: absolute;
  top: 10%;
  right: 10%;
  background-color: #043461;
  font-size: 20px;
  width: 100px;
  height: 200px;
}

.RC2 {
  position: absolute;
  top: 15%;
  left: 30%;
}

@media screen and (max-height: 480px) {
  .timer {
    width: 120px;
    height: 50px;
    // background-color: #3d75d4;
    position: absolute;
    top: 1%;
    color: black;
    right: 4%;
    // border: 1px solid;
    border-radius: 5px;
    font-size: 8px;
  }
  .scene {
    width: 100%;
  }
  .popup {
    width: 280px;
    height: 240px;
    margin: auto;
    padding: 10px;
    position: absolute;
    font-size: 12px;
    top: 10%;
    left: 30%;
    // background-color: #3d75d4;
    border-radius: 10px;
    // color: white;
    box-shadow: rgba(29, 29, 29, 0.25) 0px 54px 55px,
      rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
      rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
      
  }
  .imageTable {
    width: 80%;
    height: 350px;
    // margin: 10% auto;
    position: absolute;
    left: 1%;
    top: 5%;
  }
  .RC {
    position: absolute;
    top: 10%;
    right: 10%;
    background-color: #043461;
    font-size: 20px;
    width: 100px;
    height: 200px;
  }

  .RC1 {
    position: absolute;
    top: 10%;
    left: 20%;
    font-size: small;
  }
}
