@import "../../../styles/colors";
@import "../../../styles/fonts";

.notebook-popout {
  position: relative;
  height: 100vh;
  overflow: none;
  background: $navy7;
  .Progress-side-bar {
    width: 100px;
    height: 80%;
    background-color: #173461;
    position: absolute;
    right: 1%;
    border-radius: 10px;
    top: 8%;
  }
  .popout-content {
    height: calc(100% - 148px);
    padding: 36px;
    overflow-y: none;
    // width: 80%;
    .popout-header {
      h2 {
        display: inline-block;
        padding-bottom: 9px;
        margin: 0;
        border-bottom: 8px solid #173461;
        font-weight: 700;
        line-height: 34px;
        font-size: 42px;
        letter-spacing: 2px;
        color: $charcoal1;
      }
    }

    .popout-body {
      margin-top: 16px;
      min-height: calc(100% - 151px);

      .videoWrapper {
        position: relative;
        height: 0;
        padding-top: 25px;
        padding-bottom: 56.25%; /* 16:9 */
      }
      .videoWrapper iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
  }

  .popout-footer {
    position: absolute;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: flex-end;
    width: 100%;
    margin-top: 24px;
    padding: 8px 15px 8px 8px;
    background: #e5ecf0;
    box-shadow: 0 -8px 16px 0 rgba(0, 0, 0, 0.25);
    z-index: 2;
    border-radius: 10px;

    .popout-btn {
      width: 180px;
      height: 60px;
      font-size: 16px;
      border: none;
      border-radius: 6px;
      background-color: #3d75d4;
      color: white;
      cursor: pointer;
    }
  }
}
.Progress {
  width: 80%;
  height: 30px;
  background-color: white;
  margin: auto;
  position: absolute;
  top: 25%;
  left: 5%;
  div {
    div {
      background-color: #3d75d4 !important;
    }
  }
}
.barCompleted {
  background-color: lightblue;
  width: 80%;
}

@media screen and (max-width: 480px) {
  .notebook-popout .popout-content .popout-header h2{
    font-size: 28px;
    // margin-top: 0%;
  }
  .iconOpen{
    width: 50px;
    height: 60px;
  }
  .notebook-popout .popout-footer .popout-btn{
    width: 150px;
    padding: -10px;
  }
  .popout-footer{
    margin-bottom: -20%;

  }
}

@media screen and (max-height: 480px) {
  .notebook-popout{
    overflow: scroll;
    height: auto;
    // margin-top: 0%;
  }
  .notebook-popout .popout-content .popout-header h2{
    font-size: 28px;
    margin-top: 10%;
  }
  .iconOpen{
    width: 50px;
    height: 60px;
  }
  .notebook-popout .popout-footer .popout-btn{
    width: 150px;
    padding: -10px;
  }
 
}
