.reflectionDiv {
  width: 60%;
  margin: auto;
  table {
    td,
    th {
      padding-top: 12px;
      padding-bottom: 12px;
      text-align: left;
      // background-color: #173461;
      // color: white;
      border: 1px solid;
    }
    th {
      background-color: #173461;
      color: white;
    }
    button {
      background-color: #333333;
      height: 40px;
      color: white;
      font-size: large;
      border-radius: 10px;
    }
  }
}

@media only screen and (max-width: 480px) {
  .reflectionDiv {
    width: 100%;
    margin: auto;

    table {
      height: 400px;
      width: 100px;
      tr {
        width: 10px;
      }
      button {
        background-color: #333333;
        height: 40px;
        color: white;
        font-size: small;
        border-radius: 1%;
      }
    }
    .reflection-container {
      display: grid;
      overflow-y: scroll;

      grid-template-columns: auto auto auto auto;
      padding: 1px;
      grid-gap: 10px;
      width: 100%;
      // background-color: red;
      border: 1px solid;
      margin: auto;
      .reflection-item {
        // background-color: rgba(255, 255, 255, 0.8);
        // border: 1px solid rgba(0, 0, 0, 0.8);
        padding: 5px;
        font-size: 13px;
        width: 100px;
        height: 50px;
        margin: 10px auto;
        border-radius: 10px;
        text-align: center;
        grid-gap: 10px;
        button {
          background-color: #333333;
          height: 40px;
          color: white;
          font-size: medium;
          border-radius: 10px;
        }
      }
    }
  }
}
.reflection-container {
  display: grid;
  grid-template-columns: auto auto auto auto;
  padding: 1px;
  // background-color: lightgray;
  grid-gap: 10px;
  border-radius: 10px;
  width: 90%;
  height: 700px;

  overflow: scroll;
  margin: auto;
  border: 2px solid;
}
.reflection-item {
  // background-color: rgba(255, 255, 255, 0.8);
  // border: 1px solid rgba(0, 0, 0, 0.8);
  padding: 20px;
  font-size: 20px;
  width: 180px;
  height: 50px;
  margin: 10px auto;
  border-radius: 10px;
  text-align: center;
  grid-gap: 10px;
  // box-shadow: rgba(29, 29, 29, 0.25) 0px 54px 55px,
  //   rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
  //   rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  button {
    background-color: #333333;
    height: 40px;
    color: white;
    font-size: large;
    border-radius: 10px;
  }
}
.reflection-item:hover {
  -ms-transform: scale(1.1); /* IE 9 */
  -webkit-transform: scale(1.1); /* Safari 3-8 */
  transform: scale(1.1);
}
.reflection-item1 {
  border: 1px solid;
  background-color: #173461;
  // width: 50%;
  font-size: 20px;
  height: 50px;
  border-radius: 10px;
  color: white;
}

@media screen and (max-height: 480px) {
  .reflectionDiv{
    margin: -10% auto;
  }
  .reflection-container{
    // margin-left: -100px;
    width: 100%;
    overflow: scroll;
    border: 2px solid gray;
    // height: 500px;
    margin-bottom: 30%;
    .reflection-item1{
      width: 130px;
    }
    .reflection-item{
      width: 120px;
      font-size: 12px;
      margin-right: 10px;
      padding: 10px;
    }
  }
}